import React, { Component } from "react";
import TodoList from "./TodoList";
import AddTodo from './AddTodo'
import { todoData } from "./TodoData";
import {v4 as uuidv4} from 'uuid';

export default class TodoApp extends Component {
  state = { todos: todoData };

  markComplete = (id,title) => {
    console.log(id);
    // console.log(title);
    this.setState({todos: this.state.todos.map(todo => {
      if(todo.id === id) {
        // todo.completed = !todo.completed
        // todo.hits = todo.hits + 1
        //// or 
        return Object.assign({}, todo, { completed: !todo.completed,hits: todo.hits + 1})
      }
      return todo;
    } )})
  }

  removeTodo = (id) => {
    console.log(id);
    this.setState({todos: this.state.todos.filter( todo => todo.id !== id)
    });
  }

  addTodo = (title) => {
    console.log('New Todo tilte', title);
    const todo = {
      id : uuidv4(),
      title,
      completed: false,
      hits: 0
    }
    this.setState({todos: [...this.state.todos, todo]});
  }

  render() {
    return (
      <div className="container">
        <div className="col-md-8 offset-md-2">
          <br/>
          <h1>TodoApp <span className="badge badge-secondary">New</span></h1>
          <br/>
          <AddTodo addTodo={this.addTodo} />
          <TodoList todos={this.state.todos} markComplete={this.markComplete} removeTodo={this.removeTodo} />
        </div>
      </div>
    );
  }
}
