import React, { Component } from "react";

export default class AddTodo extends Component {
  state = { title: "" };

  handleOnChange(e) {
      console.log(e.target.value)
    this.setState({ title: e.target.value});
  }
 
/* ES6 Version */
//   handleOnChange = (e) => {
//     console.log(e.target.value)
//   this.setState({ title: e.target.value});
// }

onSubmit(e) {
    e.preventDefault();
    this.setState({ title: ''});
}

  render() {
    const { addTodo } = this.props;
    return (
      <div>
        <form onSubmit={this.onSubmit.bind(this)}>
          <div className="input-group mb-3">
              <input type="text" className="form-control" value={this.state.title} onChange={this.handleOnChange.bind(this)} placeholder="Add Todo Notes" aria-label="Add Todo Notes" aria-describedby="button-addon2"/>
              <div className="input-group-append">
              <button className="btn btn-outline-success" onClick={addTodo.bind(this, this.state.title)} id="button-addon2">Add Todo</button>
              </div>
          </div>
        </form>
      </div>
    );
  }
}
