import React, { Component } from "react";

export default class TodoItem extends Component {
  handleDelete(e) {
    console.log(e.target.name);
  }

  getTextStyle(completed) {
    return {
      textDecoration: completed ? "line-through" : "none",
    };
  }

  render() {
    const { todo, markComplete, removeTodo } = this.props;
    const { id, title, completed, hits } = todo;

    return (
      <div className={!completed ? "alert alert-primary" : "alert alert-secondary"}>
        <input type="checkbox" checked={completed ? "checked" : ""} onChange={markComplete.bind(this, id, title)} />
        <span style={this.getTextStyle(completed)}>{' '} {title} Hits: {hits} </span>
        <button type="button" className="close" onClick={removeTodo.bind(this, id)} aria-label="Close"> <span aria-hidden="true">&times;</span> </button>
      </div>
    );
  }
}
